import React, {PureComponent} from 'react';
import Label from 'r-cmui/components/Label';
import Table from 'r-cmui/components/Table';
import Row from 'r-cmui/components/Row';
import Col from 'r-cmui/components/Col';

import Demo1 from './demo1';
import Demo2 from './demo2';
import Demo3 from './demo3';
import Demo4 from './demo4';
import Demo5 from './demo5';


class Page extends PureComponent{
    render(){
        return (
            <div className="main-container">
                <h1 className="page-h1">Tree </h1>
                <blockquote className="page-tip">
                    <br/>
                </blockquote>

                <h1 className="page-h1"></h1>

                <Row>
                    <Col grid={0.5} className="code-col">
                        <Label className="code-box">
                            <Demo1></Demo1>
                        </Label>
                        <Label className="code-box">
                            <Demo3></Demo3>
                        </Label>
                        <Label className="code-box">
                            <Demo5></Demo5>
                        </Label>
                    </Col>
                    <Col grid={0.5} className="code-col">
                        <Label className="code-box">
                            <Demo2></Demo2>
                        </Label>
                        <Label className="code-box">
                            <Demo4></Demo4>
                        </Label>
                    </Col>
                </Row>

                <h2 className="page-h2">API</h2>
                <h3 className="page-h3">Uploadify</h3>

                <Table columns={[
                    {name: 'param', text: ''},
                    {name: 'desc', text: ''},
                    {name: 'type', text: ''},
                    {name: 'default', text: ''}
                ]} bordered={true} data={[
                    {param: 'className', desc: 'class', type: 'string', default: ''},
                    {param: 'style', desc: '', type: 'object', default: ''},
                    {param: 'data', desc: '', type: 'array', default: ''},
                    {param: 'url', desc: 'ajax', type: 'string', default: ''},
                    {param: 'enableCheckbox', desc: '', type: 'boolean', default: 'false'},
                    {param: 'enableSmartCheckbox', desc: '', type: 'boolean', default: 'false'},
                    {param: 'onSelect', desc: '', type: 'function(item)', default: ''},
                    {param: 'onOpen', desc: '', type: 'function(item)', default: ''},
                    {param: 'onCheck', desc: '', type: 'function(item)', default: ''}
                ]}></Table>

                <h3 className="page-h3">Methods</h3>

                <ul className="code-methods">
                    <li>
                        <i>setSmartSubChecked(item)</i>
                        <span></span>
                        <ul>
                            <li>
                                item {`String/Object`} id
                            </li>
                        </ul>
                    </li>
                    <li>
                        <i>setSubChecked(item)</i>
                        <span></span>
                        <ul>
                            <li>
                                item {`String/Object`} id
                            </li>
                        </ul>
                    </li>
                    <li>
                        <i>setItemChecked(item, checked)</i>
                        <span></span>
                        <ul>
                            <li>
                                item {`String/Object`} id
                            </li>
                            <li>
                                checked {`Number`}  0 1 
                            </li>
                        </ul>
                    </li>
                    <li>
                        <i>getSubItems(item)</i>
                        <span>Id</span>
                        <ul>
                            <li>
                                return {`Array`} 
                            </li>
                        </ul>
                    </li>
                    <li>
                        <i>getItem(itemId)</i>
                        <span>Id</span>
                        <ul>
                            <li>
                                itemId {`String`} 
                            </li>
                        </ul>
                    </li>
                    <li>
                        <i>getItemText(item)</i>
                        <span>id</span>
                        <ul>
                            <li>
                                item {`String/Object`} id
                            </li>
                        </ul>
                    </li>
                    <li>
                        <i>setItemText(item, text)</i>
                        <span>id</span>
                        <ul>
                            <li>
                                item {`String/Object`} id
                            </li>
                            <li>
                                text {`String`} 
                            </li>
                        </ul>
                    </li>
                    <li>
                        <i>setItemColor(item, color)</i>
                        <span>id</span>
                        <ul>
                            <li>
                                item {`String/Object`} id
                            </li>
                            <li>
                                color {`String`} 
                            </li>
                        </ul>
                    </li>
                    <li>
                        <i>setItemImg(item, img)</i>
                        <span>id</span>
                        <ul>
                            <li>
                                item {`String/Object`} id
                            </li>
                            <li>
                                img {`String`} 
                            </li>
                        </ul>
                    </li>
                    <li>
                        <i>getLevel(item)</i>
                        <span>Id</span>
                        <ul>
                            <li>
                                item {`String/Object`} id
                            </li>
                            <li>
                                return {`Number`} 
                            </li>
                        </ul>
                    </li>
                    <li>
                        <i>getOpenState(item)</i>
                        <span>Id</span>
                        <ul>
                            <li>
                                item {`String/Object`} id
                            </li>
                            <li>
                                return {`Number`} 
                            </li>
                        </ul>
                    </li>
                    <li>
                        <i>getSelectedItem()</i>
                        <span></span>
                        <ul>
                            <li>
                                return {`Object`} 
                            </li>
                        </ul>
                    </li>
                    <li>
                        <i>isItemChecked(item)</i>
                        <span>Id</span>
                        <ul>
                            <li>
                                item {`String/Object`} id
                            </li>
                            <li>
                                return {`Number`} 
                            </li>
                        </ul>
                    </li>
                    <li>
                        <i>checkItem(item)</i>
                        <span>Id</span>
                        <ul>
                            <li>
                                item {`String/Object`} id
                            </li>
                        </ul>
                    </li>
                    <li>
                        <i>unCheckItem(item)</i>
                        <span>Id</span>
                        <ul>
                            <li>
                                item {`String/Object`} id
                            </li>
                        </ul>
                    </li>
                    <li>
                        <i>openItem(item)</i>
                        <span>Id</span>
                        <ul>
                            <li>
                                item {`String/Object`} id
                            </li>
                        </ul>
                    </li>
                    <li>
                        <i>openAllItem()</i>
                        <span></span>
                    </li>
                    <li>
                        <i>closeItem(item)</i>
                        <span>Id</span>
                        <ul>
                            <li>
                                item {`String/Object`} id
                            </li>
                        </ul>
                    </li>
                    <li>
                        <i>closeAllItem()</i>
                        <span></span>
                    </li>
                    <li>
                        <i>selectItem(item)</i>
                        <span>Id</span>
                        <ul>
                            <li>
                                item {`String/Object`} id
                            </li>
                        </ul>
                    </li>
                    <li>
                        <i>unSelectItem(item)</i>
                        <span>Id</span>
                        <ul>
                            <li>
                                item {`String/Object`} id
                            </li>
                        </ul>
                    </li>
                    <li>
                        <i>getSubCheckedItems(item)</i>
                        <span>Id</span>
                        <ul>
                            <li>
                                item {`String/Object`} id
                            </li>
                        </ul>
                    </li>
                    <li>
                        <i>getSubUncheckedItems(item)</i>
                        <span>Id</span>
                        <ul>
                            <li>
                                item {`String/Object`} id
                            </li>
                        </ul>
                    </li>
                    <li>
                        <i>getAllChecked()</i>
                        <span></span>
                        <ul>
                            <li>
                                return {`Array`} 
                            </li>
                        </ul>
                    </li>
                    <li>
                        <i>getAllCheckedIncludeSmart()</i>
                        <span>smart</span>
                        <ul>
                            <li>
                                return {`Array`} 
                            </li>
                        </ul>
                    </li>
                    <li>
                        <i>getSubChecks(item, checked)</i>
                        <span>Id</span>
                        <ul>
                            <li>
                                item {`String/Object`} id
                            </li>
                            <li>
                                checked {`Number`} 
                            </li>
                            <li>
                                return {`Array`} 
                            </li>
                        </ul>
                    </li>
                    <li>
                        <i>getAllCheckedBranches()</i>
                        <span></span>
                        <ul>
                            <li>
                                return {`Array`} 
                            </li>
                        </ul>
                    </li>
                    <li>
                        <i>getAllCheckedLeafs()</i>
                        <span></span>
                        <ul>
                            <li>
                                return {`Array`} 
                            </li>
                        </ul>
                    </li>
                    <li>
                        <i>addItem(parent, item)</i>
                        <span></span>
                        <ul>
                            <li>
                                parent {`String/Object`} id
                            </li>
                            <li>
                                item {`Object`} 
                            </li>
                        </ul>
                    </li>
                    <li>
                        <i>deleteChildItems(item)</i>
                        <span></span>
                        <ul>
                            <li>
                                item {`String/Object`} id
                            </li>
                        </ul>
                    </li>
                    <li>
                        <i>removeItem(item)</i>
                        <span></span>
                        <ul>
                            <li>
                                item {`String/Object`} id
                            </li>
                        </ul>
                    </li>
                    <li>
                        <i>getAllBranches()</i>
                        <span></span>
                        <ul>
                            <li>
                                return {`Array`} 
                            </li>
                        </ul>
                    </li>
                    <li>
                        <i>getAllLeafs()</i>
                        <span></span>
                        <ul>
                            <li>
                                return {`Array`} 
                            </li>
                        </ul>
                    </li>
                    <li>
                        <i>updateParentCheckStatus(item)</i>
                        <span></span>
                        <ul>
                            <li>
                                item {`String/Object`} id
                            </li>
                        </ul>
                    </li>
                    <li>
                        <i>loadDynamicJSON(parent, json, cback)</i>
                        <span>JSON</span>
                        <ul>
                            <li>
                                parent {`String/Object`} id
                            </li>
                            <li>
                                json {`Object`} 
                            </li>
                            <li>
                                cback {`function`} 
                            </li>
                        </ul>
                    </li>
                </ul>
            </div>
        );
    }
}

export default Page;



