import React from 'react';
import BaseDemo from '../BaseDemo';
import FontIcon from 'r-cmui/components/FontIcon';
import Code from '../Code';
import Tree from 'r-cmui/components/Tree';

class Demo extends BaseDemo{

    componentDidMount(){
        let tree = this.refs.tree;
        tree.on('open', function(item){
            if(item.open) {
                window.setTimeout(function(){
                    tree.loadDynamicJSON(item, [{id: new Date().getTime(), text: ''}]);
                }, 300);
            }
        });
    }

    render(){
        let treeData = [{
            id: 0,
            text: '',
            open: true,
            children: [
                {
                    id: '1',
                    text: '',
                    children: [{id: '11', text: ''},{id: '12', text: ''}]
                },{
                    id: '2',
                    text: ''
                }
            ]
        }];

        return (
            <div>
                <div className="code-box-demo">
                    <Tree data={treeData} ref="tree" />
                </div>
                <div className="code-box-desc">
                    <div className="code-box-title"></div>
                    <div>
                        openloadDynamicJSON
                        <FontIcon icon={'chevron-circle-down'} ref="collapse" className="collapse" onClick={this.openCloseCode.bind(this)}></FontIcon>
                    </div>
                </div>
                <div className="code-box-src" ref="boxSrc">
                    <Code className="language-jsx">
                        {`
import Tree from 'r-cmui/components/Tree';

class Demo extends BaseDemo{

    componentDidMount(){
        let tree = this.refs.tree;
        tree.on("open", function(item){
            if(item.open) {
                window.setTimeout(function(){
                    tree.loadDynamicJSON(item, [{id: new Date().getTime(), text: ""}]);
                }, 300);
            }
        });
    }

    render(){
        let treeData = [{
            id: 0,
            text: "",
            open: true,
            children: [
                {
                    id: '1',
                    text: "",
                    children: [{id: '11', text: ""},{id: '12', text: ""}]
                },{
                    id: '2',
                    text: ""
                }
            ]
        }];

        return (
            <div className="code-box-demo">
                <Tree data={treeData} ref="tree" />
            </div>
        );
    }
}

`}
                    </Code>
                </div>
            </div>
        );
    }
}

export default Demo;



